<template>
  <div>
    <Modal v-model="wareModal" width="1000">
      <p slot="header" style="text-align: center; font-size: 20px; color: cornflowerblue;">
        <span>Ware Info</span>
      </p>
      <Table height="500" border :columns='wareColumns' :data='wareData' style="margin-top: 20px;">
        <template slot-scope="{ row, index }" slot="price">
          <Input type="number" v-model="editPrice" v-if="editWareIndex === index" />
          <span v-else>{{ row.price }}</span>
        </template>

        <template slot-scope="{ row, index }" slot="inventory">
          <Input type="number" v-model="editInventory" v-if="editWareIndex === index" />
          <span v-else>{{ row.inventory }}</span>
        </template>

        <template slot-scope="{ row, index }" slot="action">
          <div v-if="editWareIndex === index">
            <Button type="success" size="small" style="margin-right: 5px" @click="handleSave(index, 2)">保存</Button>
            <Button type="warning" size="small" @click="editWareIndex = -1">取消</Button>
          </div>
          <div v-else>
            <Button type="primary" size="small" style="margin-right: 5px" @click="handleEdit(row, index, 2)">修改</Button>
            <Button type="info" size="small" @click="buy(row)">购买</Button>
          </div>
        </template>
      </Table>
    </Modal>
    <Modal v-model="createOrderModal" width="400">
      <p slot="header" style="text-align: center; font-size: 20px; color: cornflowerblue;">
        <span>New Order</span>
      </p>
      <!-- <h1>haha</h1> -->
      <div>
        <Card style="margin:0 auto;width: 100%;margin-top: 10px; ">
          <div style="display: flex; justify-content: flex-start;">
            <div>
              <h4>OrderID:</h4>
            </div>
            <div style="margin-left: 10px;">
              <Input
              v-model="createOrderForm.order_id"
              show-word-limit
              type="textarea"
              placeholder="Enter something..."
              maxlength="15"
              :autosize="{minRows: 1}"></Input>
            </div>
          </div>
          <div style="display: flex; justify-content: flex-start;">
            <div>
              <h4>BuyerAccountID:</h4>
            </div>
            <div style="margin-left: 10px;">
              <p>{{createOrderForm.buyer_account_id}}</p>
            </div>
          </div>
          <div style="display: flex; justify-content: flex-start;margin-top: 8px;">
            <div>
              <h4>GoodName:</h4>
            </div>
            <div style="margin-left: 10px;">
              <p>{{createOrderForm.good_name}}</p>
            </div>
          </div>
          <div style="display: flex; justify-content: flex-start;margin-top: 8px;">
            <div>
              <h4>Size:</h4>
            </div>
            <div style="margin-left: 10px;">
              <p>{{createOrderForm.size}}</p>
            </div>
          </div>
          <div style="display: flex; justify-content: flex-start;margin-top: 8px;">
            <div>
              <h4>ProducePlace:</h4>
            </div>
            <div style="margin-left: 10px;">
              <p>{{createOrderForm.produce_place}}</p>
            </div>
          </div>
          <div style="display: flex; justify-content: flex-start;margin-top: 8px;">
            <div>
              <h4>Amount:</h4>
            </div>
            <div style="margin-left: 10px;">
              <Input
              v-model="createOrderForm.amount"
              type="number"
              placeholder="Enter something..."
              :autosize="{minRows: 1}"></Input>
            </div>
          </div>
          <div style="display: flex; justify-content: flex-start;">
            <div>
              <h4>Year:</h4>
            </div>
            <div style="margin-left: 10px;">
              <p>{{createOrderForm.year}}</p>
            </div>
          </div>
          <div style="display: flex; justify-content: flex-start;margin-top: 8px;">
            <div>
              <h4>Month:</h4>
            </div>
            <div style="margin-left: 10px;">
              <p>{{createOrderForm.month}}</p>
            </div>
          </div>
          <div style="display: flex; justify-content: flex-start;margin-top: 8px;">
            <div>
              <h4>Date:</h4>
            </div>
            <div style="margin-left: 10px;">
              <p>{{createOrderForm.day}}</p>
            </div>
          </div>
          <div style="display: flex; justify-content: flex-start;margin-top: 8px;">
            <div>
              <h4>Price:</h4>
            </div>
            <div style="margin-left: 10px;">
              <p>{{createOrderForm.amount*current_ware_price+current_delivery_fee}}</p>
            </div>
          </div>
          <div style="margin-top: 8px;">
            <h4>ReceiveAddress:</h4>
            <Input
            v-model="createOrderForm.receive_address"
            show-word-limit
            type="textarea"
            placeholder="Enter something..."
            maxlength="200"
            :autosize="{minRows: 2}"></Input>
          </div>
        </Card>
      </div>
      <div slot="footer">
      	<Button type="primary" size="large" long @click="submitOrder()">Submit</Button>
      </div>
    </Modal>
    <Modal v-model="createGoodModal" width="400">
      <p slot="header" style="text-align: center; font-size: 20px; color: cornflowerblue;">
        <span>Create Good</span>
      </p>
      <Form :model="createGoodForm" style="margin:auto">
        <FormItem label="Name">
          <Input
          v-model="createGoodForm.good_name"
          show-word-limit
          type="textarea"
          placeholder="Enter something..."
          maxlength="15"
          :autosize="{minRows: 1}"></Input>
        </FormItem>
        <FormItem label="Dilivery Fee">
          <Input
          v-model="createGoodForm.delivery_fee"
          type="number"
          placeholder="Enter something..."
          :autosize="{minRows: 1}"></Input>
        </FormItem>
      	<FormItem label="Description">
      		<Input
          v-model="createGoodForm.description"
          show-word-limit
          type="textarea"
          placeholder="Enter something..."
          maxlength="200"
      		:autosize="{minRows: 2}"></Input>
      	</FormItem>
      </Form>
      <div slot="footer">
      	<Button type="primary" size="large" long @click="submit()">Submit</Button>
      </div>
    </Modal>
    <h1 style="margin-top:10px; text-align: center;">Inventory</h1>
    <Table height="500" border :columns='inventoryColumns' :data='inventoryData' style="margin-top: 20px;">
      <template slot-scope="{ row, index }" slot="good_name">
        <Input type="text" v-model="editGood_name" show-word-limit maxlength="15" v-if="editGoodIndex === index" />
        <span v-else>{{ row.good_name }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="description">
        <Input type="text" v-model="editDescription" show-word-limit maxlength="200" v-if="editGoodIndex === index" />
        <span v-else>{{ row.description }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="delivery_fee">
        <Input type="number" v-model="editDelivery_fee" v-if="editGoodIndex === index" />
        <span v-else>{{ row.delivery_fee }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="action">
        <div v-if="editGoodIndex === index">
          <Button type="success" size="small" style="margin-right: 5px" @click="handleSave(index, 1)">保存</Button>
          <Button type="warning" size="small" @click="editGoodIndex = -1">取消</Button>
        </div>
        <div v-else>
          <Button type="primary" size="small" style="margin-right: 5px" @click="handleEdit(row, index, 1)">修改</Button>
          <Button type="info" size="small" @click="wareInfo(index)">具体信息</Button>
          <Button type="error" size="small" @click="remove(index)">移除</Button>
        </div>
      </template>
    </Table>
    <Button type="primary" style="margin-top: 10px; width: 100px;" v-on:click="createGood">
      <Icon type="ios-add" size="20"></Icon>
      Add
    </Button>
    <Button type="info" style="margin-top: 10px; margin-left: 10px; width: 100px;" v-on:click="upLoad">
      <Icon type="ios-cloud" size="20"></Icon>
      Upload
    </Button>
  </div>
</template>

<script>
  //import data from '../mock/good.js'
  //import data2 from '../mock/wareInfo.js'
  export default {
    props:{
      dataSource: Number
    },
    data() {
      return {
        wareModal: false,
        wareColumns: [{
          title: 'ware_id',
          key: 'ware_id',
          sortable: true
        },{
          title: 'size',
          key: 'size',
          sortable: true
        },{
          title: 'produce_place',
          key: 'produce_place',
          sortable: true
        },{
          title: 'price',
          slot: 'price',
          sortable: true,
          filters: [{
              label: '0 ~ 50',
              value: 1
            },
            {
              label: '50 ~ 80',
              value: 2
            },
            {
              label: '80 ~ 100',
              value: 3
            }
          ],
          filterMultiple: false,
          filterMethod(value, row) {
            if (value === 1) {
              return row.price <= 50;
            } else if (value === 2) {
              return row.price > 50 && row.price <= 80;
            } else if (value === 3) {
              return row.price > 80;
            }
          }
        },{
          title: 'inventory',
          slot: 'inventory',
          sortable: true,
          filters: [{
              label: '0 ~ 200',
              value: 1
            },
            {
              label: '200 ~ 500',
              value: 2
            },
            {
              label: '500 ~ 1000',
              value: 3
            }
          ],
          filterMultiple: false,
          filterMethod(value, row) {
            if (value === 1) {
              return row.inventory <= 200;
            } else if (value === 2) {
              return row.inventory > 200 && row.inventory <= 500;
            } else if (value === 3) {
              return row.inventory > 500;
            }
          }
        },{
          title: '操作',
          slot: 'action',
          align: 'center'
        }],
        wareData: [],
        createGoodModal: false,
        createGoodForm: {
          good_name: '',
          delivery_fee: 0,
          description: ''
        },
        inventoryColumns: [{
            title: 'good_name',
            //slot: 'good_name',
            key: 'good_name',
            sortable: true
          },
          {
            title: 'delivery_fee',
            slot: 'delivery_fee',
            sortable: true,
            filters: [{
                label: '<=5',
                value: 1
              },
              {
                label: '>5',
                value: 2
              }
            ],
            filterMultiple: false,
            filterMethod(value, row) {
              if (value === 1) {
                return row.delivery_fee <= 5;
              } else if (value === 2) {
                return row.delivery_fee > 5;
              }
            }
          },
          {
            title: 'description',
            slot: 'description',
            sortable: true
          },
          {
            title: '操作',
            slot: 'action',
            align: 'center'
          }
        ],
        inventoryData: [],
        editGoodIndex: -1, // 当前聚焦的输入框的行数
        editGood_name: '',
        editDelivery_fee: 0,
        editDescription: '',

        editWareIndex: -1,
        editPrice: 0,
        editInventory: 0,

        allWareList:[],

        createOrderModal: false,
        current_delivery_fee: 0,
        current_good_name:'',
        current_ware_id: '',
        current_ware_price: 0,
        createOrderForm:{
          order_id: 'OrderID',
          buyer_account_id: 'AccountId000000',
          good_name: '',
          size: '',
          produce_place: '',
          amount: 0,
          year: 0,
          month: 0,
          day: 0,
          price: 0,
          receive_address: ''
        }
      }
    },
    mounted() {
      var url

      //加载good表
      if(this.dataSource == 1){
        url = '/good/info'
      }
      else {
        url = '/g/good/info'
      }
      this.$axios
        .get(url)
        .then(response => {
          this.inventoryData = response.data
          this.$Message.success('成功加载Good数据')
        })
        .catch(error => {
          this.$Message.error('加载Good数据失败')
        })

      //加载ware表
      if(this.dataSource == 1){
        url = '/ware/info'
      }
      else {
        url = '/g/ware/info'
      }
      this.$axios
          .get(url)
          .then(response =>{
            this.allWareList = response.data
            this.$Message.success('成功加载Ware数据')
          })
          .catch(error=>{
            this.$Message.error('加载Ware数据失败')
          })
    },
    methods: {
      submitOrder(){
        //删掉库存
        for(var i = 0; i < this.allWareList.length; i++){
          if(this.allWareList[i].good_name === this.current_good_name && this.allWareList[i].ware_id === this.current_ware_id){
            this.allWareList[i].inventory -= this.createOrderForm.amount
            break
          }
        }
        //将新订单放入缓存
        this.createOrderForm.price = this.createOrderForm.amount * this.current_ware_price
        this.$emit('createOrder', this.createOrderForm)
        this.createOrderModal = false
      },
      buy(row) {
        this.current_ware_id = row.ware_id
        this.current_ware_price = row.price
        this.createOrderForm.order_id = ''
        this.createOrderForm.good_name = this.current_good_name
        this.createOrderForm.size = row.size
        this.createOrderForm.produce_place = row.produce_place
        this.createOrderForm.amount = 0
        var now = new Date()
        this.createOrderForm.year = now.getFullYear()
        this.createOrderForm.month = now.getMonth() + 1
        this.createOrderForm.day = now.getDate()
        this.createOrderForm.price = 0
        this.createOrderForm.receive_address = ''
        this.createOrderModal = true

      },
      submit(){
        //创建good
        const newGood = {
          good_name: this.createGoodForm.good_name,
          delivery_fee: this.createGoodForm.delivery_fee,
          description: this.createGoodForm.description
        }
        this.inventoryData.push(newGood)
        //创建四个对应的ware
        const newWare0 = {
          good_name: this.createGoodForm.good_name,
          ware_id: 0,
          size: 'big',
          produce_place: 'native',
          price: 0,
          inventory: 0
        }
        this.allWareList.push(newWare0)
        const newWare1 = {
          good_name: this.createGoodForm.good_name,
          ware_id: 1,
          size: 'big',
          produce_place: 'imported',
          price: 0,
          inventory: 0
        }
        this.allWareList.push(newWare1)
        const newWare2 = {
          good_name: this.createGoodForm.good_name,
          ware_id: 2,
          size: 'small',
          produce_place: 'native',
          price: 0,
          inventory: 0
        }
        this.allWareList.push(newWare2)
        const newWare3 = {
          good_name: this.createGoodForm.good_name,
          ware_id: 3,
          size: 'small',
          produce_place: 'imported',
          price: 0,
          inventory: 0
        }
        this.allWareList.push(newWare3)
        //关闭对话框
        this.createGoodModal = false
      },
      createGood(){
        this.createGoodModal = true
      },
      remove(index) {
        var good_name = this.inventoryData[index].good_name
        this.inventoryData.splice(index, 1);
        for (var i = 0; i < this.allWareList.length;) {
          if (this.allWareList[i].good_name === good_name) {
            this.allWareList.splice(i, 1)
          }
          else {
            i++
          }
        }
      },
      handleEdit(row, index, type) {//type==1修改Good，type==2修改Ware
        if(type == 1){
          this.editGood_name = row.good_name;
          this.editDelivery_fee = row.delivery_fee;
          this.editDescription = row.description;
          this.editGoodIndex = index;
        }
        else if (type == 2){
          this.editPrice = row. price
          this.editInventory = row.inventory
          this.editWareIndex = index
        }
      },
      handleSave(index, type) {
        if(type == 1){
          this.inventoryData[index].good_name = this.editGood_name;
          this.inventoryData[index].delivery_fee = this.editDelivery_fee;
          this.inventoryData[index].description = this.editDescription;
          this.editGoodIndex = -1;
        }
        else if (type == 2){
          this.wareData[index].price = this.editPrice;
          this.wareData[index].inventory = this.editInventory;
          this.editWareIndex = -1;
        }

      },
      wareInfo(index) {
        this.wareData = []
        var good_name = this.inventoryData[index].good_name
        this.current_good_name = good_name
        this.current_delivery_fee = this.inventoryData[index].delivery_fee
        for(var i = 0; i < this.allWareList.length; i++){
          if(this.allWareList[i].good_name == good_name){
            this.wareData.push(this.allWareList[i])
          }
        }
        this.wareModal = true
      },
      upLoad(){
        var url

        //更新good表
        if(this.dataSource == 1){
          url = '/good/update'
        }
        else {
          url = '/g/good/update'
        }
        this.$axios
            .post(url, {
              list: this.inventoryData
            })
            .then(response => {
              this.$Message.success("成功将Good数据同步至Hive")
            })
            .catch(error => {
              this.$Message.error("同步Good数据失败！")
            })

        //更新ware表
        if(this.dataSource == 1){
          url = '/ware/update'
        }
        else {
          url = '/g/ware/update'
        }
        this.$axios
            .post(url, {
              list: this.allWareList
            })
            then(response => {
              this.$Message.success("成功将Ware数据同步至Hive")
            })
            .catch(error => {
              this.$Message.error("同步Ware数据失败！")
            })
      }
    }
  }
</script>

<style>
</style>



